.assistant {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.assistant-head {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 40px;
    z-index: var(--max-zindex);
    padding-top: calc(env(safe-area-inset-top) + 10px);
    padding-bottom: 10px;
    padding-left: env(safe-area-inset-left);
    padding-right: env(safe-area-inset-right);
}

.assistant-main {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding-left: env(safe-area-inset-left);
    padding-right: env(safe-area-inset-right);
    padding-top: calc(env(safe-area-inset-top) + 10px + 40px + 10px);
    padding-bottom: calc(env(safe-area-inset-bottom) + 70px + 30px);
    overflow: auto;
}

.assistant-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 70px;
    padding-top: 10px;
    z-index: var(--max-zindex);
    padding-left: env(safe-area-inset-left);
    padding-right: env(safe-area-inset-right);
    padding-bottom: env(safe-area-inset-bottom);
}

.assistant-head-div {
    width: calc(100% - 30px);
    height: 100%;
    padding-left: var(--border-padding);
    padding-right: var(--border-padding);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.assistant-head-logo {
    width: 40px;
    height: 40px;
}

.head-notification {
    margin-left: 10px;
    height: 100%;
    max-width: calc(100% - 50px);
    display: flex;
    gap: 10px;
    overflow: auto;
}

.head-notification-item {
    flex-shrink: 0;
    height: calc(100% - 2px);
    padding: 0 10px;
    background: var(--main-bg-color-2);
    border-radius: var(--border-radius);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    font-size: var(--font-size-mid);
}

.assistant-notification {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding-top: calc(env(safe-area-inset-top) + 40px + 10px);
    padding-bottom: calc(env(safe-area-inset-bottom) + 70px + 10px);
    padding-left: calc(env(safe-area-inset-left) + var(--border-padding));
    padding-right: calc(env(safe-area-inset-right) + var(--border-padding));
    display: flex;
    flex-direction: row;
    overflow: auto;
    gap: 10px;
    z-index: var(--mid-zindex);
}

.assistant-notification-item {
    flex-shrink: 0;
    width: 100%;
    height: 99%;
    border-radius: var(--border-radius);
    background: var(--main-bg-color-2);
}

.assistant-notification-item-title {
    width: calc(100% - 20px);
    height: calc(100% - 30px - 130px);
    text-align: center;
    padding-top: 30px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: var(--font-size-big-1);
}

.assistant-notification-item-time {
    width: 100%;
    height: 50px;
    font-size: var(--font-size-big);
    color: var(--time-color);
}

.assistant-notification-item-operate {
    width: 100%;
    height: 80px;
    font-size: var(--font-size-big-1);
}

.assistant-notification-item-operate-btn {
    width: 50%;
    height: 100%;
}

.animate-scale-in {
    animation: animate-scale-in 0.5s ease-in-out;
}

@keyframes animate-scale-in {
    from {
        transform: scale(0.1);
        opacity: 0;
    }
    50% {
        transform: scale(1.1);
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}
