:root {
    --primary-color: #3955ff;
    --primary-glass-color: rgba(57, 85, 255, 0.5);
    --success-color: #80ff81;
    --warn-color: #F6CE46;
    --danger-color: #EA4D3D;
    --money-color: #FFD700;
    --time-color: #F6CE46;
    --font-size-mini: 13px;
    --font-size-small: 16px;
    --font-size-mid: 20px;
    --font-size-big: 30px;
    --font-size-big-1: 50px;
    --border-radius: 25px;
    --backdrop-filter: blur(6px) saturate(180%);
    --mid-zindex: 50;
    --max-zindex: 100;
    --system-head-color: var(--main-bg-color-1);
    --main-shadow: 2px 4px 12px var(--main-text-color-2);
    --primary-shadow: 2px 4px 12px var(--primary-color);
    --border-padding: 15px;
}

:root[theme ='day'] {
    --main-bg-color-1: rgba(242, 242, 247);
    --main-bg-color-2: rgba(255, 255, 255);
    --main-text-color-1: rgba(0, 0, 0);
    --main-text-color-2: rgba(0, 0, 0, .5);
    --main-glass-color: rgba(255, 255, 255, 0.3);
    --main-glass-shadow-color: rgba(255, 255, 255);
    --main-bg: linear-gradient(270deg, rgb(174, 121, 204) 3%, rgb(19, 102, 236) 96%);
}

:root[theme='night'] {
    --main-bg-color-1: rgba(0, 0, 0);
    --main-bg-color-2: rgba(28, 28, 30);
    --main-text-color-1: rgba(255, 255, 255);
    --main-text-color-2: rgba(255, 255, 255, .5);
    --main-glass-color: rgba(28, 28, 30, 0.3);
    --main-glass-shadow-color: rgba(255, 255, 255, 0.5);
    --main-bg: linear-gradient(270deg, rgba(174, 121, 204, 0.5) 3%, rgba(19, 102, 236, 0.5) 96%);
}

.glass-theme {
    background: var(--main-glass-color) !important;
    backdrop-filter: var(--backdrop-filter) !important;
    box-shadow: inset 1px 1px 0 var(--main-glass-shadow-color), inset 0 0 5px var(--main-glass-shadow-color);
}

.bg-glass-theme {
    background: var(--main-glass-color) !important;
    backdrop-filter: var(--backdrop-filter) !important;
}

.primary-glass-theme {
    background: var(--primary-glass-color) !important;
    backdrop-filter: var(--backdrop-filter) !important;
    box-shadow: inset 1px 1px 0 var(--main-glass-shadow-color), inset 0 0 5px var(--main-glass-shadow-color);
}

.dc-leftMove {
    animation: ky-leftMove .5s ease-in-out;
}

@keyframes ky-leftMove {
    from {
        transform: translateX(-20px);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

body {
    background: var(--main-bg-color-1);
    color: var(--main-text-color-1);
    font-size: var(--font-size-small);
}