.flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.time-count-down {
    width: 100%;
    height: 100%;
}

.close-btn {
    color: var(--main-text-color-2);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    cursor: pointer;
}

.del-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--danger-color);
    font-weight: bold;
    cursor: pointer;
}

.ok-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--main-text-color-2);
    font-weight: bold;
    cursor: pointer;
}

.ok-btn.active {
    color: var(--success-color);
}

.div-mask {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: var(--mid-zindex) -1;
    background: var(--main-glass-color);
}

div {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}