/*chat-input*/
.chat-input-div {
    padding: 0 15px;
    width: calc(100% - 30px);
    z-index: var(--max-zindex);
    display: flex;
}

.chat-input {
    width: calc(100% - 10px - 50px);
    height: 60px;
    border: none;
    outline: none;
    padding: 0 10px;
    margin-right: 10px;
    border-radius: var(--border-radius);
    background: var(--main-bg-color-2);
    color: var(--main-text-color-1);
    font-size: var(--font-size-mid);
}

.chat-input:focus {
    box-shadow: inset 1px 1px 0 var(--primary-glass-color), inset 0 0 5px var(--primary-glass-color);
}

.chat-input-btn {
    margin: 10px 0;
    width: 40px;
    height: 40px;
    font-size: var(--font-size-mid);
    color: var(--main-text-color-2);
    cursor: pointer;
    border-radius: 50%;
}

.chat-input-btn.active {
    font-weight: bold;
}

/*chat-list*/
.chat-list {
    width: calc(100% - 30px);
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.chat-list-item-time {
    width: 100%;
    height: 20px;
    line-height: 20px;
    color: var(--main-text-color-2);
    margin-bottom: 5px;
}

.chat-list-item-time.right {
    text-align: right;
}

.chat-list-item-left {
    display: flex;
    align-items: center;
    justify-content: left;
    width: 100%;
}

.chat-list-item-right {
    display: flex;
    align-items: center;
    justify-content: right;
    width: 100%;
}

.chat-list-item-content {
    max-width: calc(100% - 50px - 20px);
    padding: 10px;
    font-size: var(--font-size-mid);
    display: flex;
    gap: 10px;
    align-items: center;
}

.chat-list-item-content.left {
    border-radius: var(--border-radius);
    background: var(--main-bg-color-2);
    color: var(--main-text-color-1);
}

.chat-list-item-content.right {
    border-radius: var(--border-radius);
    background: var(--primary-color);
    color: white;
}